//BANNER SECTION
.bannerParent{
  width: 100%;
  height: auto;

}

.banner {
  width:$fullWidth;
  min-height: 621px;
  background-image:
          //linear-gradient(to bottom, rgba(0, 206, 205, 0.52), rgba(139, 219, 219, 0.73)),
          //url('../images/banner.jpg');
        linear-gradient(to bottom, rgba(0, 206, 205, 0.72), rgba(0, 206, 205, 0.93)), url(/images/banner.jpg);
  margin:$centerOnPage;
  background-repeat:no-repeat;
  background-size:cover;
}

.bannerData {
  @include flex;
  width:$pageWrap;
  margin: auto;
  padding: 42px 0;
  color: $white; //inherited value

}

//BANNER CONTENTS
.homePostCodeParent {
  @include uniformAlignment;
}

.box {
  width: 511px;
  min-height: 431px;
  border-radius: 20px;
  background-color: #ffffffad;
  padding: 20px 30px;
  text-align:center;

  p {
    color: $black;
    font-size: 25px;
    width: 66%;
    margin: auto;
    font-weight: 500;
    letter-spacing: -1.25px;
  }
}

.homePromo {
  @include uniformAlignment;
  margin: auto 0;

  p{
    font-size: 1.5em;
    //width: 86%;
  }
}

.promoList {
  width:80%;
  li{
    list-style:none;
    font-size: 1.4em;
    line-height: 40px;
  }
}

.browseButton {
  text-align:center;
  font-size: 1.2em;
  a{
    color: $darkerGrey;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: -0.7px;
    line-height: 19px;
  }

}
.aquaChevron {
  color: $aqua;
  position:$absolute;
  left: 420px;
  top: 25px;
}

.postcode-autocomplete-cont {
  float: left;
  width: 100%;
  overflow: hidden;
  -webkit-transition: 1s linear;
  -moz-transition: 1s linear;
  -ms-transition: 1s linear;
  -o-transition: 1s linear;
  transition: 1s linear;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  -ms-transition-delay: 0s;
  -o-transition-delay: 0s;
  transition-delay: 0s;
  margin-top: 20px;
  .ajax-box {
    float: left;
    width: 100%;
    padding: 10px;
    border-radius: 10px;
    background-color: #fff;
    border: 2px solid #00cecd;
    max-height: 222px;
    overflow: auto;
  }

}



